/* General body styling */
body {
    background: #afe1af;
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    font-family: Georgia, 'Times New Roman', Times, serif;
    margin: 0;
    padding: 0;
}

/* Login container to center everything */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/*-----------------------------------------Left Content--------------------------------------------*/

.left-content {
    max-width: 45%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-right: 40%;
    align-items: center; /* Align items to the start (left side) */
    text-align: center; /* Ensure text alignment is also to the left */
    padding-right: 50px; /* Optional: add some padding to separate it from the right content */
}

.left-content .main-text {
    font-size: 36px;
    font-weight: bold;
    margin-bottom: 20px;
    color: #4F7942;
    margin-left: 0%;
}

.left-content .sub-text {
    font-size: 18px;
    line-height: 1.5;
    color: #000000;
}

/*-----------------------------------------Error Message--------------------------------------------*/
.errorMessage{
    text-align: center;
    border: 2px solid;
    border-color: rgb(255, 0, 0);
    background-color: rgba(255, 0, 0, 0.25);
    padding: 10px;
    display: none;
    vertical-align: middle;
    width: 200px;
    margin:auto;
}
.errorMessage p{
    text-align: center;
}
/*-----------------------------------------Right Content--------------------------------------------*/
 .box-forms {
    width: 300px;
    height: 80%;
    background: #faf9f6;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .1), 0 8px 16px rgba(0, 0, 0, .1);
    position: absolute;
    top: 5%;
    right: 220px;
    border-radius: 10px;
    text-align: center;
    padding:  30px;
  }

  .box-forms .logo img {
    width: 50%;
    margin-top: 0;
}

.box-forms .h3 {
    font-size: 14px;
    margin-bottom: 20px;
    color: #4F7942;
}


/* Input and button styling */
.box-forms label {
    display: block;
    margin-bottom: 3px;
    font-size: 15px;
    text-align: left;
}

.box-forms input {
    width: 95%;
    height: 20px;
    margin-bottom: 17px;
    padding: 5px;
    border: 2px solid #4F7942;
    border-radius: 5px;
}

.box-forms button {
    width: 90%;
    padding: 8px;
    background-color: #AFE1AF;
    border-radius: 5px;
    cursor: pointer;
    border: 2px solid #67a16f;
    font-weight: bold;
    font-size: 16px;
    color: #4F7942;
    align-items: center;
}

.log-in-link {
    text-align: center;
    margin-top: 20px;
}

.link a {
    color: #4F7942;
    font-weight: bold;
    text-decoration: none;
    font-size: 14px;
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
    .box-forms {
        width: 100%;
        padding: 20px;
    }

    .box-forms .logo img {
        width: 80%;
    }
}
