.user-theme-container {
  display: grid;
  width: 60rem;
  grid-template-columns: 1fr 1fr;
  grid-gap: 20px;
  padding: 20px;
  color: black;
  background-color: transparent;
  border-radius: 10px;
  border: 2px solid #B8e2a3;
  box-shadow: 0px 0px 10px rgba(27, 25, 25, 0.1);
  margin-left: 2.5rem;
  margin-bottom: 5rem;
}

.theme-options {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
  grid-column: span 2;
}

.theme-item {
  display: flex;
  flex-direction: column;
}

.theme-item label {
  margin-bottom: 5px;
  font-weight: bold;
}

.theme-item input[type="text"] {
  width: 50%;
  padding: 8px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.theme-items {
  display: flex;
  flex-direction: column;
  width: 50%;
  height: 5vh;
  padding: 8px;
  margin-bottom: 5px;
  font-weight: bold;
}

.controls-container {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-left: 2.5rem;
  margin-top: 20px;
}

.file-upload-container {
  white-space: nowrap;
  margin-left: 10rem;
  display: flex;
  align-items: center;
  gap: 1rem;
}

.upload-button {
  background-color: #B8e2a3;
  text-align: center;
  margin-left: 3rem;
  color: black;
  padding: 6px 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: Arial, sans-serif;
  font-weight: bold;
  white-space: nowrap;
}
.save-button {
  background-color: #B8e2a3;
  width: 10rem;
  font-weight: bold;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  font-family: Arial, sans-serif;
  margin-top: 2rem;
  margin-left: 26rem;
  color: black;
  padding: 1px; 
  background-color: #B8e2a3;
  font-size: 0.9rem; /* Adjust the font size as necessary */
  white-space: nowrap; /* Ensure the text stays on one line */
}

.upload-button:hover,
.save-button:hover {
  background-color: #3e6336;
  color: white;
}
