.users-container {
    padding: 20px;
    color: #333;
    background-color: #f7f7f7;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
    margin: 2rem auto;
    max-width: 80%;
    margin-bottom: 2rem;
  }
  
  .users-container h1 {
    text-align: center;
    margin-bottom: 20px;
    font-family: Arial, sans-serif;
    font-size: 24px;
    color: #4F7942;
  }
  
  .users-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .users-table th, .users-table td {
    padding: 12px 15px;
    text-align: left;
    border-bottom: 1px solid #ddd;
  }
  
  .users-table th {
    background-color: #B8e2a3;
    color: black;
    font-family: Arial, sans-serif;
    font-weight: bold;
  }
  
  .users-table tr:hover {
    background-color: #f1f1f1;
  }
  
  .users-table td {
    font-family: Arial, sans-serif;
  }
  
  .users-table td:first-child {
    font-weight: bold;
  }
  
  @media screen and (max-width: 768px) {
    .users-container {
      width: 100%;
      padding: 10px;
    }
  
    .users-table th, .users-table td {
      padding: 8px 10px;
    }
  }
  